import React from "react";
import Work from "../Components/Work";
import { personalDetails, workDetails, eduDetails, practicePlatforms, blogs, certificates } from "../Details";

function About() {
  return (
    <main className="container mx-auto max-width pt-10 pb-20 ">
      <section>
        <h1 className="text-2xl text-dark-heading dark:text-light-heading md:text-4xl xl:text-5xl xl:leading-tight font-bold">
          About Me
        </h1>
        <p className="text-content py-8 lg:max-w-3xl">{personalDetails.about}</p>
      </section>
      <section>
        <h1 className="text-2xl text-dark-heading dark:text-light-heading md:text-4xl xl:text-5xl xl:leading-tight font-bold">
          Work Experience
        </h1>
        {React.Children.toArray(
          workDetails.map(({ Position, Company, Location, Type, Duration }) => (
            <Work
              position={Position}
              company={Company}
              location={Location}
              type={Type}
              duration={Duration}
            />
          ))
        )}
      </section>
      <section>
  <h1 className="text-2xl pt-10 text-dark-heading dark:text-light-heading md:text-4xl xl:text-5xl xl:leading-tight font-bold">
    My Practice Platforms
  </h1>
  <ul className="text-content py-8 lg:max-w-3xl">
    {practicePlatforms.map(({ name, link, description }) => (
      <li key={name} className="py-2 flex items-start">
        <span className="text-yellow-500 mr-2">★</span>
        <a href={link} target="_blank" rel="noopener noreferrer" className="text-blue-500 underline">
          {name}
        </a>
        : {description}
      </li>
    ))}
  </ul>
</section>
      <section>
        <h1 className="text-2xl pt-10 text-dark-heading dark:text-light-heading md:text-4xl xl:text-5xl xl:leading-tight font-bold">
          Education
        </h1>
        {React.Children.toArray(
          eduDetails.map(({ Position, Company, Location, Type, Duration }) => (
            <Work
              position={Position}
              company={Company}
              location={Location}
              type={Type}
              duration={Duration}
            />
          ))
        )}
      </section>
      <section>
  <h1 className="text-2xl pt-10 text-dark-heading dark:text-light-heading md:text-4xl xl:text-5xl xl:leading-tight font-bold">
    Blogs
  </h1>
  <ul className="text-content py-8 lg:max-w-3xl">
    {blogs.map(({ title, link, description }) => (
      <li key={title} className="py-2 flex items-start">
        <span className="text-yellow-500 mr-2">★</span>
        <a href={link} target="_blank" rel="noopener noreferrer" className="text-blue-500 underline">
          {title}
        </a>
        : {description}
      </li>
    ))}
  </ul>
</section>
<section>
  <h1 className="text-2xl pt-10 text-dark-heading dark:text-light-heading md:text-4xl xl:text-5xl xl:leading-tight font-bold">
    Certificates
  </h1>
  <ul className="text-content py-8 lg:max-w-3xl">
    {certificates.map(({ title, issuer, link }) => (
      <li key={title} className="py-2 flex items-start">
        <span className="text-yellow-500 mr-2">★</span>
        <a href={link} target="_blank" rel="noopener noreferrer" className="text-blue-500 underline">
          {title}
        </a>
        : Issued by {issuer}
      </li>
    ))}
  </ul>
</section>
    </main>
  );
}

export default About;
